package material

import kotlinx.coroutines.MainScope
import kotlinx.coroutines.launch
import kotlinx.css.*
import org.w3c.dom.get
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.dom.*
import styled.css
import styled.styledDiv
import kotlin.browser.document

class MaterialApp : RComponent<RProps, RState>() {
    override fun RBuilder.render() {
        requireMaterialButton()
        requireMaterialIconButton()
        header {
            h1 { +"Material Components" }
        }
        styledDiv {
            css {
                padding(5.px)
            }
            h2 { +"Buttons" }
            child(ButtonList::class) {}
            h2 { +"Cards" }
            child(CardList::class) {}
        }
        MainScope().launch {
            val query = document.querySelectorAll(".toggle-button")
            for(i in 0 until query.length) {
                val node = query[i]
                node?.let { MDCIconButtonToggle(it.parentElement) }
            }
        }
    }
}
