package confExplorer

import kotlinx.coroutines.*
import kotlinx.css.Color
import kotlinx.css.color
import kotlinx.css.fontFamily
import react.*
import react.dom.div
import react.dom.h3
import styled.css
import styled.styledH1
import kotlin.browser.window

external interface ConfExplorerAppState: RState {
    var currentVideo: Video?
    var unwatchedVideos: List<Video>
    var watchedVideos: List<Video>
}

data class Video(val id: Int, val title: String, val speaker: String, val videoUrl: String)

class ConfExplorerApp: RComponent<RProps, ConfExplorerAppState>() {
    override fun RBuilder.render() {
        styledH1 {
            css {
                fontFamily = "sans-serif"
                color = Color.indianRed
            }
            +"Hello, React+Kotlin/JS!"
        }
        div {
            h3 { +"Videos to watch" }
            videoList {
                videos = state.unwatchedVideos
                selectedVideo = state.currentVideo
                onSelectVideo = { video ->
                    setState {
                        currentVideo = video
                    }
                }
            }
            h3 { +"Videos watched" }
            videoList {
                videos = state.watchedVideos
                selectedVideo = state.currentVideo
                onSelectVideo = { video ->
                    setState {
                        currentVideo = video
                    }
                }
            }
        }
        state.currentVideo?.let { currentVideo ->
            videoPlayer {
                video = currentVideo
                unwatchedVideo = currentVideo in state.unwatchedVideos
                onWatchedButtonPressed = { video ->
                    if (video in state.unwatchedVideos) {
                        setState {
                            unwatchedVideos -= video
                            watchedVideos += video
                        }
                    }
                    else {
                        setState {
                            watchedVideos -= video
                            unwatchedVideos += video
                        }
                    }
                }
            }
        }
    }

    override fun ConfExplorerAppState.init() {
        unwatchedVideos = listOf()
        watchedVideos = listOf()

        val mainScope = MainScope()
        mainScope.launch {
            val videos = fetchVideos()
            setState {
                unwatchedVideos = videos
            }
        }
    }
}

suspend fun fetchVideo(id: Int): Video =
    window.fetch("https://my-json-server.typicode.com/kotlin-hands-on/kotlinconf-json/videos/$id")
            .await()
            .json()
            .await()
            .unsafeCast<Video>()

suspend fun fetchVideos(): List<Video> = coroutineScope {
    (1..25).map { id ->
        async {
            fetchVideo(id)
        }
    }.awaitAll()
}